.register-container {
    max-width: 400px;
    margin: auto;
    margin-top: 200px;
    padding: 2rem;
    border: 1px solid #ccc;
    border-radius: 10px;
    font-family: sans-serif;
  }
  
  .register-container h2 {
    text-align: center;
  }
  
  .register-container input,
  .register-container textarea {
    width: 100%;
    margin: 0.5rem 0;
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .register-container button {
    width: 100%;
    padding: 0.75rem;
    background-color: #3498db;
    color: white;
    border: none;
    border-radius: 5px;
    margin-top: 1rem;
    cursor: pointer;
  }
  
  .error-message {
    color: red;
    font-size: 0.9rem;
  }
  
  .success-message {
    color: green;
    font-size: 0.9rem;
  }
  .centered-text {
    text-align: center;
    margin-top: 1rem;
  }
  