.permissions-container {
    padding: 20px;
    background-color: #fff;
    border-radius: 6px;
    max-width: 800px;
    margin: 0 auto;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .permissions-container h3 {
    font-size: 22px;
    margin-bottom: 20px;
    color: #333;
  }
  
  .permissions-select-user {
    margin-bottom: 25px;
  }
  
  .permissions-select-user label {
    font-weight: bold;
    margin-right: 10px;
  }
  
  .permissions-select-user select {
    padding: 8px 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
  }
  
  .permissions-checkboxes {
    border-top: 1px solid #eee;
    padding-top: 20px;
  }
  
  .permissions-checkboxes h4 {
    margin-bottom: 10px;
    color: #555;
  }
  
  .permissions-checkboxes h5 {
    margin: 15px 0 8px;
    font-size: 16px;
    color: #666;
  }
  
  .permission-item {
    display: flex;
    align-items: center;
    margin-bottom: 6px;
    font-size: 14px;
    color: #444;
  }
  
  .permission-item input[type="checkbox"] {
    margin-right: 8px;
  }
  
  .save-button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 15px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .save-button:hover {
    background-color: #45a049;
  }
  